@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: auto;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    padding: 0 20px;
    width: auto;
    height: 60px;
    background-color: #7096C4;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.logo {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.logo > img {
    width: 23px;
    height: 20px;
    margin-right: 15px;
}

.title {
    font-family: Roboto, sans-serif;
    padding: 0;
}

.button {
    font-family: 'Nunito', sans-serif;
    font-weight: bold;
    border: solid white 2px;
    border-radius: 5px;
    width: 97.5px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    padding: 10px;
    background: none;
    color: white;
    cursor: pointer;
    transition-duration: 0.5s;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.button > img {
    width: 12px;
    height: 11px;
    margin-right: 7px;
}

.button:hover {
    font-family: 'Nunito', sans-serif;
    font-weight: bold;
    border-radius: 5px;
    width: 97.5px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    padding: 10px;
    background-color: white;
    color: black;
    cursor: pointer;
}
