.container {
    background-color: #536e8e;
    padding: 20px;
    height: auto;
    display: flex;
    flex-direction: column;
}

.headings {
    font-size: 1.17em;
    font-weight: bolder;
    padding-bottom: 10px;
    color: white;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.movie-lists {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.movie-box {
    background-color: white;
    text-align: center;
    width: 170px;
    height: 350px;
    border-radius: 7px;
    margin: 15px;
    transition-duration: 0.4s;
}

.movie-box:hover {
    transform: scale(1.1);
    box-shadow: 0 8px 12px 0 rgba(0,0,0,0.3)
}

.movie-contents{
    padding: 0;
}

.movie-poster {
    width: 170px;
    height: 250px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    /* padding-top: 12px; */
}

.movie-title {
    font-size: 1em;
    font-weight: bolder;
    padding-top: 3px;
    padding-bottom: 1px;
    color: rgb(73, 45, 45);
}

.movie-genre {
    font-size: 0.7em;
    padding-top: 0;
    padding-bottom: 10px;
    color: rgb(73, 45, 45);
}

.button_container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.info {
    font-family: Times, serif;
    font-weight: bold;
    font-style: italic;
    width: 30px;
    height: 30px;
    background: none;
    border: solid 1px black;
    border-radius: 50%;
    text-align: center;
    transition-duration: 0.4s;
}

.info:hover {
    cursor: pointer;
    background-color: black;
    color: white
}

.wishlist {
    width: 30px;
    height: 30px;
    background: none;
    border: solid 1px black;
    border-radius: 50%;
    text-align: center;
    transition-duration: 0.4s;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.wishlist > img {
    width: 12px;
    height: 11px;
}

.wishlist:hover {
    cursor: pointer;
    background-color: black;
    color: white
}

/* Information Box */

.info_list{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.info_poster {
    width: 170px;
    height: 250px;
    border-radius: 7px;
    margin: 20px;
    transition-duration: 0.4s;
}

.info_poster:hover {
    box-shadow: 0 8px 12px 0 rgba(0,0,0,0.3);
}

.info_holder{
    background-color: white;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 700px;
    border-radius: 7px;
    transition-duration: 0.4s;
}

.info_holder:hover {
    box-shadow: 0 8px 12px 0 rgba(0,0,0,0.3);
}

.info_contents {
    padding: 10px;
}

.info-title {
    font-size: 1.5em;
    font-weight: bolder;
    padding-top: 3px;
    padding-bottom: 1px;
    color: rgb(73, 45, 45);
}

.info-genre {
    font-size: 1em;
    padding-top: 3px;
    padding-bottom: 1px;
    color: rgb(73, 45, 45);
}

.info-rating {
    font-size: 1em;
    padding-top: 3px;
    padding-bottom: 1px;
    color: rgb(73, 45, 45);
}

.info-desc {
    font-size: 1em;
    padding-top: 3px;
    padding-bottom: 1px;
    color: rgb(73, 45, 45);
    text-align: justify;
}

.close_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 20px
}

.info-close {
    font-family: 'Nunito', sans-serif;
    font-weight: bold;
    border: solid white 2px;
    border-radius: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    padding: 10px;
    background: none;
    color: white;
    cursor: pointer;
    transition-duration: 0.5s;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.info-close:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

