}

.wishlist_container {
    background-color: #536e8e;
    padding: 20px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.remove {
    font-family: 'Nunito', sans-serif;
    font-weight: bold;
    width: 30px;
    height: 30px;
    background: none;
    border: solid 1px black;
    border-radius: 50%;
